import Vector2 from "../data/Vector2";
export default class Vertex extends HTMLElement {
    static new(trans, type) {
        const node = document.createElement('m-vertex');
        trans.appendChild(node);
        const style = node.style;
        style.position = "absolute";
        style.width = "0px";
        style.height = "0px";
        switch (type) {
            case Vertex.TYPE_LT:
                break;
            case Vertex.TYPE_RT:
                style.left = "100%";
                break;
            case Vertex.TYPE_RB:
                style.top = "100%";
                style.left = "100%";
                break;
            case Vertex.TYPE_LB:
                style.top = "100%";
                break;
        }
        return node;
    }
    get positionScreen() {
        const bound = this.getBoundingClientRect();
        return new Vector2(bound.x, bound.y);
    }
}
Vertex.TYPE_LT = 0;
Vertex.TYPE_RT = 1;
Vertex.TYPE_RB = 2;
Vertex.TYPE_LB = 3;
customElements.define("m-vertex", Vertex);
